<template>
    <div class="body">
        <div class="input_box">
            <input class="input" type="text" @keydown="find($event)" placeholder="Insira um nome">
        </div>
        <p class="result">{{searchResult}}</p>
    </div>
</template>

<script>
export default {

    data(){
        return{
            searchResult:""
        }
    },

    methods:{
    
        find(e){

            var axios = require("axios");
            const configUrl = require("../config/config");

            var config = {
            method: "get",
            url: `${configUrl.api.url}/attendance`,
            headers: {},
            };

            axios(config).then(function (response) {
                for(let responses in response.data.result){
                    if(response.data.result[responses].email.toUpperCase().includes(e.target.value.toUpperCase()) && e.target.value!=""){
                       return `Resultado: ${response.data.result[responses].email}, Pontuação: ${response.data.result[responses].pontuactiom}`;
                    }
                } 
            }).then(value => this.searchResult = value)
        }
    }
}
</script>

<style scoped>

.body{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input {
  border: none;
  height: 30px;
  background-color: transparent;
  padding: 0% 2% 0% 2%;
  width: 150px;
  text-align: center;
}

.input_box{
    width: 150px;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.219);
    border-radius: 5px;
}
.input_box:hover {
  transition: 0.5s;
  transform: scale(1.02);
}

.input:focus{
    outline: 0;
}

.result{
    font-family: Bebas;
    font-size: 15pt;
    text-align: center;
    overflow: visible;
}

</style>