import { createStore } from "vuex";

const store = createStore({
  state: {
    cards: [],
    role: "",
    logged: false,
    users: [],
    gamification: []
  },
  getters: {
    getCards(state) {
      return state.cards;
    },

    haveCard(state) {
      return !(state.cards.length > 0);
    },

    getRole(state) {
      return state.role;
    },

    getLog(state) {
      return state.logged;
    },

    getUsers(state) {
      let v = [];

      for (let i in state.users) {
        v.push(state.users[i].username);
      }

      return v;
    },

    getGamefication(state) {

      return state.gamification;

    }

  },
  mutations: {
    setGamefication(state) {
      var axios = require("axios");

      const configUrl = require("./config/config");

      var config = {
        method: "get",
        url: `${configUrl.api.url}/attendance`,
        headers: {},
      };

      axios(config)
        .then(function (response) {
        
          state.gamification = response.data.result;

        })
        .catch(function (error) {
          console.log(error);
        });
    },

    setCards(state) {
      var axios = require("axios");

      const configUrl = require("./config/config");

      var config = {
        method: "get",
        url: `${configUrl.api.url}/meeting`,
        headers: {},
      };

      axios(config)
        .then(function (response) {
          state.cards = response.data.result;
          console.log(state.cards);
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    setRole(state, newRole) {
      state.role = newRole;
    },

    setLogged(state, newLog) {
      state.logged = newLog;
    },

    setUsers(state) {
      var axios = require("axios");

      const configUrl = require("./config/config");

      let array = [];

      var config = {
        method: "get",
        url: `${configUrl.api.url}/user`,
        headers: {},
      };

      axios(config)
        .then(function (response) {
          array = response.data.users;
          state.users = array;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
  },
});

export default store;
