<template>
  <div>
    <table cellpadding="8">
      <thead>
        <tr>
          <th>Email</th>
          <th>Pontos</th>
        </tr>
      </thead>
      <tbody v-for="game in gamefication" :key="game">
        <tr>
          <td>{{ game.email }}</td>
          <td>{{ game.pontuactiom }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  created() {
    this.$store.commit("setGamefication");
  },

  computed: {
    ...mapGetters({
      gamefication: "getGamefication",
    }),
  },
};
</script>

