<template>
  <div class="cards">
    <div v-for="meet in getCards" :key="meet.idVar">
      <MeetCard
        :title="meet.title"
        :when="meet.when"
        :formLink="meet.form"
        :id="meet.id"
        :adm="x"
        :online="meet.online"
        :local = "meet.local"
      />
    </div>
  </div>
</template>

<script>
import MeetCard from "../components/MeetCard.vue";

import { mapGetters } from "vuex";

export default {
  props: {
    x: Boolean,
  },
  components: {
    MeetCard,
  },
  created() {
    this.$store.commit("setCards");
  },

  computed: {
    ...mapGetters({
      getCards: "getCards",
    }),
  },
};
</script>

<style scoped>
.cards {
  padding-bottom: 10px;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  column-gap: 10px;
  row-gap: 15px;
}
</style>
