<template>
  <div id="body">
    <HeaderBody id="header" />

    <div id="page_content">
      <Title2Font text="Cards de Reunião" class="title" />

      <div class="create_card_box">
        <div class="create_card">
          <CreateMeetCard />
        </div>
        <div class="display_card">
          <TextFont text="Reuniões Criadas:" class="display_card_title" />
          <DisplayMeetCardVue @click="deleteCard($event)" :x="true" />
        </div>
      </div>

      <hr />

      <Title2Font text="Resultados do Gamefication" class="title" />

      <TextFont class="small_text" text="Pesquisa por Nome" />

      <SearchBar class="search" />

      <TextFont class="small_text" text="Resultado Geral - (Top 10)" />

      <VueChart />

      <hr />

      <Title2Font text="Registrar Presenças" class="title" />

      <AttendanceRegister />

      <hr />

      <div v-if="role == 'admin'">
      
        <Title2Font text="Gerenciar Usuários" class="title" />

        <ManageUser />

        <DeleteButton class="delete_button"/>

      </div>
    </div>
    <FooterBody id="footer" />
  </div>
</template>

<script>
import DisplayMeetCardVue from "@/components/DisplayMeetCard.vue";
import HeaderBody from "../components/HeaderBody.vue";
import FooterBody from "@/components/FooterBody.vue";
import CreateMeetCard from "../components/CreateMeetCard.vue";
import TextFont from "@/components/TextFont.vue";
import Title2Font from "@/components/Title2Font.vue";
import VueChart from "@/components/VueChart.vue";
import ManageUser from "@/components/ManageUser.vue";
import SearchBar from "@/components/SearchBar.vue";
import AttendanceRegister from "@/components/AttendanceRegister.vue";
import DeleteButton from "@/components/DeleteButton.vue";
import { mapGetters } from "vuex";

export default {
  components: {
    DisplayMeetCardVue,
    HeaderBody,
    FooterBody,
    CreateMeetCard,
    TextFont,
    Title2Font,
    VueChart,
    SearchBar,
    ManageUser,
    AttendanceRegister,
    DeleteButton
  },

  computed: {
    ...mapGetters({
      role: "getRole",
    }),
  },

  methods: {
    deleteCard(e) {
      if (e.target.id == "deleteCard") {

        let option = window.confirm(
            `Deseja excluir o card ${e.target.parentElement.childNodes[1].innerHTML}?`
        );

        if (option) {
        var axios = require("axios");

        const configUrl = require("../config/config");
        var store = this.$store;

        var config = {
          method: "delete",
          url: `${configUrl.api.url}/meeting/${e.target.parentElement.id}`,
          headers: {},
        };

        axios(config)
          .then(function (response) {
            console.log(JSON.stringify(response.data));
            store.commit("setCards");
          })
          .catch(function (error) {
            console.log(error);
          });
        }

        }
      }
    },
  }
</script>

<style scoped>
#body {
  padding: 0%;
  margin: 0%;
  height: 100vh;
}
#header {
  position: fixed;
  top: 0;
}
#footer {
  position: fixed;
  bottom: 0;
}

#page_content {
  padding: 110px 2% 70px 2%;
  display: flex;
  row-gap: 25px;
  align-items: center;
  flex-direction: column;
}

.small_text {
  font-size: 12pt;
}

.delete_button{
  margin-top: 100px;
}

.search {
  margin-bottom: -30px;
}

.title {
  text-align: center;
}

.create_card_box {
  display: flex;
  width: 100%;
  column-gap: 15px;
  align-items: center;
}

.create_card {
  flex: 0.1;
}

.display_card {
  margin-top: 10px;
  height: 300px;
  border-radius: 10px;
  border: 8px solid #f38234;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.247);
  flex: 0.9;
  overflow-y: scroll;
  padding: 2%;
}

.display_card_title {
  text-align: center;
  margin-bottom: 1%;
}

hr {
  border: 0;
  border-top: 1px dashed #595959;
  margin: 2% 0% 1% 0%;
  width: 100%;
}

@media screen and (max-width: 500px) {
  #page_content {
    padding: 26% 2% 15% 2%;
  }
  .display_card {
    max-height: 400px;
    min-width: 92%;
    margin-top: 0;
  }

  .create_card_box {
    flex-direction: column;
    row-gap: 15px;
  }
}
</style>
