<template>
  <div class="body">
    <div class="form">
      <div class="input_box">
        <input type="text" placeholder="User" class="input" v-model="user" />
        <input
          type="password"
          placeholder="Senha"
          class="input"
          v-model="pass"
        />
        <div class="adm_box">
          <input
            type="radio"
            id="adm"
            value="adm"
            name="type"
            @click="role = 'admin'"
          />
          <label for="adm" class="label">Administrador</label>
        </div>
        <div class="mod_box">
          <input
            type="radio"
            id="mod"
            value="mod"
            name="type"
            @click="role = 'moderator'"
          />
          <label for="mod" class="label">Moderador</label>
        </div>

        <div class="button_div">
          <button class="button" @click="createUser()">Cadastrar</button>
        </div>
      </div>
    </div>

    <div class="display">
      <span class="span">Clique para remover!</span>

      <div v-for="users in getUsers" :key="users">
        <p class="users" @click="removeUser($event)">{{ users }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      user: "",
      pass: "",
      role: "",
    };
  },

  created() {
    this.$store.commit("setUsers");
  },

  computed: {
    ...mapGetters({
      getUsers: "getUsers",
    }),
  },

  methods: {
    removeUser(e) {

    if(window.confirm(`Deseja deletar o usuário: ${e.target.innerHTML}`)){

      var axios = require("axios");
      var data = JSON.stringify({
        "username": e.target.innerHTML
      });

      const configUrl = require("../config/config"); 
      var store = this.$store;

      var config = {
        method: "delete",
        url: `${configUrl.api.url}/user`,
        headers: {
          "Content-Type": "application/json",
        },
        data: data,
      };

      axios(config)
        .then(function (response) {
          console.log(JSON.stringify(response.data));
          store.commit("setUsers");
        })
        .catch(function (error) {
          console.log(error);
        });
    }
    },

    createUser() {
      if(this.user!="" && this.pass!=""){
      let user = this.user;
      let pass = this.pass;

      let role = this.role;

      var axios = require("axios");
      var data = JSON.stringify({
        username: user,
        password: pass,
        role: role,
      });

      const configUrl = require("../config/config");
      var store = this.$store;

      var config = {
        method: "post",
        url: `${configUrl.api.url}/user`,
        headers: {
          "Content-Type": "application/json",
        },
        data: data,
      };

      axios(config)
        .then(function (response) {
          console.log(JSON.stringify(response.data));
          window.alert("Criado com sucesso!")
          store.commit("setUsers");
        })
        .catch(function (error) {
          console.log(error);
        });
    }else{
      window.alert("Digite valores válidos!");
    }
    },
  },
};
</script>

<style scoped>
.body {
  display: flex;
  column-gap: 30px;
}

.users {
  margin-bottom: 1%;
  cursor: pointer;
}

.users:hover {
  color: red;
}

.input_box {
  display: flex;
  flex-direction: column;
  row-gap: 6px;
}

p {
  font-family: Roboto;
  font-weight: bold;
  color: #545454;
}

.display {
  width: auto;
  padding: 15px;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.247);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  border: 7px solid #f38234;
  max-height: 150px;
}

.span {
  margin-top: 2px;
  font-family: roboto;
  color: #545454;
  font-weight: bold;
}

.form {
  display: flex;
  column-gap: 10px;
  justify-content: center;
  background-color: #f38234;
  padding: 20px 0% 20px 0%;
  width: 200px;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.247);
  border-radius: 10px;
  max-height: 150px;
}

.input {
  border-radius: 5px;
  border: none;
  height: 30px;
  background-color: white;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.219);
  padding: 0% 2% 0% 2%;
  width: 150px;
  text-align: center;
}

.input:hover {
  transition: 0.5s;
  transform: scale(1.02);
}

.input:focus {
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.219);
  outline: 0;
}

.button {
  background-color: white;
  border-radius: 5px;
  height: 30px;
  width: 100%;
  border: none;
  font-size: small;
  color: black;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  cursor: pointer;
}

.button_div {
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.247);
  border-radius: 5px;
  text-align: center;
}

.button_div:hover {
  transform: scale(1.02);
}

.adm_box,
.mod_box {
  display: flex;
  align-items: center;
}

.label {
  font-size: medium;
  font-weight: bold;
  color: white;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

@media screen and (max-width: 500px) {
  .body {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
    align-items: center;
  }
}
</style>
