<template>
  <div class="body_head">
    <router-link to="/">
      <img class="logo" src="../assets/logoHeader.png" alt="logoHeader" />
    </router-link>
    <div class="right_elements">
      <component :is="component" />
    </div>
  </div>
</template>

<script>
import HeaderMenu from "./HeaderMenu.vue";
import HeaderMenuMobile from "./HeaderMenuMobile.vue";

export default {
  components: {
    HeaderMenu,
    HeaderMenuMobile,
  },

  data() {
    return {
      component: "",
    };
  },

  created() {
    if (window.screen.width <= 700) {
      this.component = HeaderMenuMobile;
    } else {
      this.component = HeaderMenu;
    }
  },
};
</script>

<style scoped>
.body_head {
  width: 100%;
  display: flex;
  height: 75px;
  background: #f38234;
  border-radius: 0px 0px 20px 20px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.192);
  justify-content: space-between;
  align-items: center;
}

.logo {
  transform: scale(0.6);
  margin-left: 2%;
  filter: drop-shadow(4px 4px 4px #00000057);
}

.right_elements {
  width: 22%;
  margin-right: 2%;
}
</style>
