<template>
  <div class="menu_box">
    <font-awesome-icon
      class="menu_icon"
      icon="fa-bars fa-solid"
      @click="openMobileMenu"
    />
    <div class="menu_open">
      <HeaderButtons class="options" text="Home" destination="/" />
      <HeaderButtons class="options" text="Login" destination="/login" />
    </div>
  </div>
</template>

<script>
import HeaderButtons from "./HeaderButtons.vue";
export default {
  components: {
    HeaderButtons,
  },

  data() {
    return {
      open: false,
    };
  },

  methods: {
    openMobileMenu() {
      let menu = document.querySelector(".menu_open");

      if (this.open) {
        menu.style.display = "none";
        this.open = !this.open;
      } else {
        menu.style.display = "flex";
        this.open = !this.open;
      }
    },
  },
};
</script>

<style scoped>
.menu_box {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}
.menu_icon {
  color: white;
  font-size: 35px;
}

.menu_open {
  position: absolute;
  flex-direction: column;
  padding: 2% 4% 2% 4%;
  align-items: center;
  margin-top: 46%;
  margin-right: 5%;
  width: auto;
  height: auto;
  background-color: white;
  border: 3px solid #f38234;
  border-radius: 10px;
  display: none;
}
</style>
