<template>
  <div class="form">
    <div class="meet_type">
      <h4 class="meet_type_tile">A reunião vai ser:</h4>
      <div class="input_offline">
        <input
          type="radio"
          id="offline"
          name="meet_type"
          @click="online = false"
          class="meet_type_input"
          checked
        />
        <label for="offline" class="label">Presencial</label>
      </div>
      <div class="input_offline">
        <input
          type="radio"
          id="online"
          name="meet_type"
          @click="online = true"
          class="meet_type_input"
        />
        <label for="online" class="label">On-line</label>
      </div>
    </div>

    <div class="card_body">
      <div class="meet_title">
        <input
          class="input"
          type="text"
          id="title"
          @focus="clearInput($event)"
          v-model="title"
        />
      </div>

      <div class="meet_time">
        <input class="input" type="date" id="date" v-model="date" />
        <input class="input" type="time" id="time" v-model="time" />
      </div>

      <hr />

      <div class="meet_link">
        <input
          class="input"
          type="text"
          id="local"
          @focus="clearInput($event)"
          v-model="local"
        />
        <input
          class="input"
          type="url"
          id="form"
          @focus="clearInput($event)"
          v-model="form"
        />
      </div>
    </div>

    <div class="button_div">
      <input
        type="button"
        class="button"
        @click="buildCard"
        value="Criar Card"
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: "Titulo",
      time: "",
      date: "",
      form: "Link do Formulário",
      local: "Local/Link da Reunião",
      online: false,
    };
  },

  methods: {
    buildCard() {
      let dateVar = new Date(this.date);
      dateVar = dateVar.toLocaleDateString("pt-BR", { timeZone: "UTC" });

      var axios = require("axios");
      var data = JSON.stringify({
        title: this.title,
        online: this.online,
        local: this.local,
        when: `${dateVar} ${this.time}`,
        form: this.form,
      });

      const configUrl = require("../config/config");

      var store = this.$store;

      var config = {
        method: "post",
        url: `${configUrl.api.url}/meeting`,
        headers: {
          "Content-Type": "application/json",
        },
        data: data,
      };

      axios(config)
        .then(function (response) {
          console.log(JSON.stringify(response.data));
          store.commit("setCards");
        })
        .catch(function (error) {
          console.log(error);
        });

      this.title = "Titulo";
      this.form = "Link do Formulário";
      this.local = "Local/Link da Reunião";

      

    },

    clearInput(e) {
      switch (e.target.id) {
        case "title":
          if (this.title == "Titulo") {
            this.title = "";
          }
          break;
        case "form":
          if (this.form == "Link do Formulário") {
            this.form = "";
          }
          break;
        case "local":
          if (this.local == "Local/Link da Reunião") {
            this.local = "";
          }
          break;
      }
    },
  },
};
</script>

<style scoped>
.form {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  justify-content: center;
  margin-top: 10px;
}

.input {
  border-radius: 5px;
  border: none;
  height: 30px;
  background-color: transparent;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.219);
  padding: 0% 2% 0% 2%;
  width: 150px;
  text-align: center;
}

.input:hover {
  transition: 0.5s;
  transform: scale(1.02);
}

.input:focus {
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.219);
  outline: 0;
}

.button {
  background-color: transparent;
  border-radius: 5px;
  height: 30px;
  width: 100%;
  border: none;
  font-size: small;
  font-weight: bold;
  color: black;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  cursor: pointer;
}

.button_div {
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.247);
  border-radius: 5px;
  text-align: center;
}

.button_div:hover {
  transform: scale(1.02);
}

.card_body {
  background-color: white;
  height: 230px;
  width: 200px;
  border-radius: 10px;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.247);
}

.meet_title {
  background-color: #f38234;
  border-radius: 10px 10px 0px 0px;
  height: 50px;
  text-align: center;
  padding-top: 9%;
}

.meet_time {
  margin: 5% 0% 0% 0%;
  text-align: center;
  color: black;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  display: flex;
  column-gap: 8px;
  padding: 0px 12px 0px 12px;
}

hr {
  border: 0;
  border-top: 1px dashed #f38234;
  margin-left: 8%;
  margin-right: 8%;
  margin-top: 5%;
  margin-bottom: 5%;
}

