  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  margin-bottom: 0;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.247);
  border-radius: 10px;
  padding: 9% 7% 9% 7%;
