<template>
  <div id="form_div">
    <img src="../assets/seepro.png" alt="logo" />
    <input class="input_login" type="text" placeholder="Usuário" v-model="user"/>
    <input class="input_login" type="password" placeholder="Senha" v-model="pass" />

    <button id="button_login" @click="tryLogin()">Logar</button>
  </div>
</template>

<script>
export default {

  data(){
    return{
      user:"",
      pass: "",
    }
  },

  methods: {

    
    tryLogin() {

      let user = this.user;
      let pass = this.pass;
      let router = this.$router;
      let store = this.$store;

      var axios = require("axios");
      var data = JSON.stringify({
        "username": user,
        "password": pass
      });


      const configUrl = require("../config/config"); 

      var config = {
        method: "post",
        url: `${configUrl.api.url}/login`,
        headers: {
          "Content-Type": "application/json",
        },
        data: data,
      };

      axios(config)
        .then(function (response) {
          store.commit("setRole", response.data.role);
          store.commit("setLogged", true);
          router.push('/adm');
        })
        .catch(function (error) {
          console.log(error);
          window.alert("Algo deu errado!");
        });
    },
  },
};
</script>

<style scoped>
#form_div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: auto;
}

.input_login {
  width: 30vw;
  height: 1vh;
  padding: 20px;
  border: #f38234 solid;
  font-size: larger;
  margin-bottom: 10px;
  border-radius: 20px;
}

img {
  margin: -12%;
}

#button_login {
  margin-top: 9%;
  height: 6vh;
  width: 25vw;
  font-size: larger;
  border-radius: 15px;
  background-color: #f38234;
  color: white;
  border: none;
}

#button_login:active {
  transform: scale(0.98);
}

@media screen and (min-width: 900px) {
  #form_div {
    transform: scale(0.6);
  }

  .input_login {
    width: 15vw;
  }

  #button_login {
    width: 10vw;
    height: 9vh;
  }

  #button_login:hover {
    cursor: pointer;
    box-shadow: 3px 3px 10px wheat;
  }
}
</style>
