<template>
  <div class="body">
    <TextFont class="small_text" text="Deletar Pontuações!" />

    <div class="button_div">
      <button class="button" @click="show = true">Delete</button>
    </div>

    <div class="danger" v-if="show">
      <h4 class="text">Deseja Limpar Todos Pontos?</h4>
      <p class="text">Para prosseguir digite "confirmar"</p>
      <input type="text" v-model="confirm" />
      <div class="button_box">
        <div class="button_div">
          <button class="button" @click="deleteAll()">Deletar</button>
        </div>
        <div class="button_div green">
          <button class="button green" @click="show = false">Cancelar</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TextFont from "./TextFont.vue";

export default {
  data() {
    return {
      show: false,
      confirm: "",
    };
  },

  components: {
    TextFont,
  },

  methods: {
    deleteAll() {
      if (this.confirm == "confirmar") {
        var axios = require("axios");
        var data = "";

        const configUrl = require("../config/config");


        var store = this.$store;

        var config = {
          method: "delete",
          url: `${configUrl.api.url}/attendance`,
          headers: {},
          data: data,
        };

        axios(config)
          .then(function (response) {
            console.log(JSON.stringify(response.data));
            store.commit('setGamefication');
            window.alert("Limpando Tudo!")
          })
          .catch(function (error) {
            console.log(error);
          });
      }else{
        window.alert("Menssagem de confirmação incorreta!");
      }
    },
  },
};
</script>

<style scoped>
.button {
  background-color: transparent;
  border-radius: 5px;
  height: 30px;
  width: 100%;
  border: none;
  font-size: small;
  font-weight: bold;
  color: red;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  cursor: pointer;
  padding: 5px;
}

.small_text {
  font-size: 12pt;
}
.text {
  margin: 0;
  text-align: center;
}

.button_div {
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.247);
  border-radius: 5px;
  text-align: center;
  width: 30%;
  border: red solid 5px;
}

.button_div:hover {
  transform: scale(1.02);
}

.green {
  color: green;
  border-color: green;
}
.body {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  justify-content: center;
  align-items: center;
}

.danger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 15px;
  background-color: white;
  border: solid #f38234 5px;
  border-radius: 10px;
  padding: 30px;
}

.button_box {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 50px;
}
</style>
