<template>
  <div class="body">
    <div class="display">
      <div>
        <TextFont class="title_meet" text="Selecione" />
        <TextFont class="title_meet" text="uma Reunião" />
      </div>

      <div v-for="meet in getCards" :key="meet" class="meet_display">
        <input
          type="radio"
          name="meet"
          :value="meet.id"
          id="meet"
          @click="setId($event)"
        />
        <label for="meet">{{ meet.title }}</label>
      </div>
    </div>

    <div class="form">
      <div class="final_box">
        <label class="label" for="final">Fim da Reunião:</label>
        <input type="datetime-local" id="final" class="input" v-model="final" />
      </div>
      <div class="import_box">
        <label id="label" for="file">Adicionar Arquivo</label>
        <input type="file" id="file" ref="file" accept=".csv" />
      </div>

      <div class="button_div">
        <button class="button" @click="registerAttendance()">Registrar!</button>
      </div>
    </div>
  </div>
</template>

<script>
import papa from "papaparse";
import axios from "axios";
import { mapGetters } from "vuex";
import TextFont from "./TextFont.vue";

export default {
  data() {
    return {
      final: "",
      meetId: "",
    };
  },

  components: {
    TextFont,
  },

  computed: {
    ...mapGetters({
      getCards: "getCards",
    }),
  },

  methods: {
    setId(e) {
      this.meetId = e.target.value;
    },

    async registerAttendance() {

      let csvFile = this.$refs.file.files[0];

      if (csvFile == undefined) {
        window.alert("Por favor, insira um arquivo");
        return;
      }

      const configUrl = require("../config/config");


      let store = this.$store;

      let start = "";
      let final = this.final;
      let id = this.meetId;

      

      for (let meet in this.getCards) {
        if (this.getCards[meet].id == id) {
          start = this.getCards[meet].when;
        }
      }

      var data = {
        start: start,
        end: final,
        meeting_id: id
      }

      papa.parse(csvFile, {
        header: true,
        dynamicTyping: true,
        skipEmptyLines: true,
        preview: 100,
        complete(result) {
          let values = [], headers = [];

          for (const key in result.data[0]) {
            headers.push(key);
          }

          result.data.forEach((element) => {
            values.push({
              email: element[headers[1]],
              time: element[headers[0]],
            });
          });

          data.attendance = values;

          var config = {
            method: "post",
            url: `${configUrl.api.url}/attendance`,
            headers: {
              "Content-Type": "application/json",
            },
            data: data,
          };

          axios(config)
            .then(function (response) {
              console.log(response.data);
              store.commit("setGamefication");
              window.alert("Presenças registradas com sucesso!");
            })
            .catch(function (error) {
              console.log(error);
            });

        },
      });

    }
  },

};
</script>

<style scoped>
.body {
  display: flex;
  column-gap: 30px;
  align-items: center;
  justify-content: center;
}
.form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 10px;
  padding: 20px;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.247);
  border-radius: 10px;
  background-color: #f38234;
  text-align: center;
  min-height: 130px;
  max-height: 130px;
}

.title_meet {
  font-size: 12pt;
  text-align: center;
  color: white;
  font-weight: bold;
}
.input {
  border-radius: 5px;
  border: none;
  height: 30px;
  background-color: white;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.219);
  padding: 0% 2% 0% 2%;
  width: auto;
  text-align: center;
}

.display {
  height: auto;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  justify-content: center;
  width: auto;
  background-color: #f38234;
  border-radius: 10px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.219);
  padding: 15px;
  min-height: 140px;
  max-height: 140px;
  overflow-y: scroll;
}

.meet_display {
  display: flex;
}

.label {
  margin-bottom: 5px;
}

.final_box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.input:hover {
  transition: 0.5s;
  transform: scale(1.02);
}

.input:focus {
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.219);
  outline: 0;
}

#file {
  display: none;
}

#label {
  cursor: pointer;
  border: white solid;
  border-radius: 5px;
  padding: 4px;
  color: black;
  font-weight: 100;
  background-color: white;
  font-size: small;
}

.import_box {
  margin-top: 3px;
}

.button {
  background-color: white;
  border-radius: 5px;
  height: 28px;
  width: 100%;
  border: none;
  font-size: small;
  color: black;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  cursor: pointer;
}

.button_div {
  border-radius: 5px;
  text-align: center;
  width: 110px;
}

.button_div:hover {
  transform: scale(1.02);
}

label {
  font-size: medium;
  font-weight: bold;
  color: white;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

@media screen and (max-width: 500px) {
  .body {
    flex-direction: column;
    row-gap: 10px;
  }
}
</style>
